/*=== mixins ===*/
.icon-arr {
  font-size: 6px;
  fill: #000;
}

body {
  background: #fff;
  font-size: 50px;
  font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600)));
  line-height: 1.4;
  font-family: "Clear Sans Regular", sans-serif;
  font-weight: normal;
}

@media (max-width: 600px) {
  body {
    font-size: 16px;
  }
}

@media (min-width: 1000px) {
  body {
    font-size: 50px;
  }
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  background: none;
  outline: none;
  border: 0;
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 50px;
  padding: 5px 15px;
  position: relative;
  min-width: 200px;
  color: #fff;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.4;
  font-family: "Clear Sans Bold", sans-serif;
  font-weight: normal;
  text-transform: uppercase;
  letter-spacing: 1.4px;
  background: #e86278;
  transition: background 0.25s;
}

.btn:hover {
  background: #e54c65;
}

.btn:active {
  top: 1px;
}

.btn_primary {
  background: #6dca14;
}

.btn_primary:hover {
  background: #60b312;
}

.btn_secondary {
  background: #fff;
  font-size: 12px;
  color: #000;
}

.btn_secondary:hover {
  background: #f2f2f2;
}

.inner {
  max-width: 1110px;
  margin: 0 auto;
}
